import axios from 'axios';

export default {
	data() {
		return {
			users: {},
			DB_URL: process.env.VUE_APP_DB_URL,
			endpoint: process.env.VUE_APP_USERS_ENDPOINT,
		};
	},
	methods: {
		async getUsers() {
			console.log(process.env.DB_URL);
			this.users = await axios.get(`${this.DB_URL}/${this.endpoint}`);
			return this.users;
		},
	},
};
